.marquee-container {
  display: flex;
  align-items: center;
  overflow: hidden;
}

.marquee {
  font-size: 2.6rem;
  line-height: 1.2;
  padding: 24px;
  white-space: nowrap;
  animation: marquee 30.5s infinite linear;
}

.marquee:after {
  content: 'SLOW FOOD = GOOD FOOD - CIRCUIT COURT = INGRÉDIENTS D\'ICI';
}

@keyframes marquee {
  0% { transform: translateX(0)}
  100% { transform: translateX(-50%)}
}